import React, { Component } from 'react'

import {  FormattedMessage } from "react-intl";
import {
    Row,
    Col,
    Button
} from 'patternfly-react';
import { SimpleTable } from '../ui/SimpleTable';
import { getCollectionTypes, getContentById, getTemplate } from '../api/Api';
import { Link } from 'react-router-dom';
import { BTN_ADD_NEW_CONTENT } from '../helper/Constant';
import { parseData } from '../helper/Helper';
const strapiDomain = `${process.env.REACT_APP_STRAPI_TARGET_URL}`;

export default class Config extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedContent: [],
            name: null,
            nameTwo: null,
            collectionTypes: [],
            selectedContentId: 0,
            selectedTemplateId: 'default',
            templateList: []
        };
        this.handleAddNewContent = this.handleAddNewContent.bind(this);
    }

    componentDidMount = async () => {
        let contentTypes = await getCollectionTypes();
        contentTypes = contentTypes.data.filter(obj => {
            return obj && (obj.uid && obj.uid.startsWith("api::")) && obj.isDisplayed;
        });
        const contentTypeRefine = [];
        contentTypes.length && contentTypes.forEach(element => {
            contentTypeRefine.push({ label: element.info.pluralName })
        });
        // JSON.parse(this.props.collType)

        const parsedCollType = parseData(this.props.collType);
        if (Object.keys(parsedCollType).length && this.props.selectedContentId) {
            let { data: templatesList } = await getTemplate(parsedCollType.value ? parsedCollType.value : '');
            this.setState({ templateList: templatesList, collectionTypes: templatesList })
            this.fetchContentById(parsedCollType.value, this.props.selectedContentId);
        }

        // if (this.props.selectedContentName && this.props.selectedContentId) {
        //     let { data: templatesList } = await getTemplate(this.props.selectedContentName ? this.props.selectedContentName : '');
        //     this.setState({ templateList: templatesList, collectionTypes: templatesList })
        //     this.fetchContentById(this.props.selectedContentName, this.props.selectedContentId);
        // }
    }

    fetchContentById = async (collectionType, contentId) => {
        const content = await getContentById(collectionType, contentId);
        this.setState({ selectedContent: [content] })
    }

    onChangeTemplateId = (event) => {
        this.setState({ selectedTemplateId: event.target.value })
    }

    handleTypeaheadChangeContentType = selected => {
        let selectedContentType = selected.map(option => option.label);
        this.setState({ templateList: selectedContentType });
    };

     /**
     * Navigate to Strapi dashboard/login page in a new tab on browser
     */
      handleAddNewContent = () => {
        let strapiContentManagerUrl = `${strapiDomain}/admin/content-manager`;
        var newWindow = window.open(strapiContentManagerUrl, '_blank');
        if(newWindow) {
            newWindow.focus();
        }
    }

    render() {
        return (
            <form className="form-horizontal SingleContentConfigForm well">
                <Row>
                    <Col xs={12}>
                        <div>
                            <div>
                                <span className="icon fa fa-puzzle-piece" title="Widget" />
                                <h5 className="SingleContentConfigFormBody__widgetTitle"><FormattedMessage id="app.content" /></h5>
                                <div className="SectionTitle SectionTitle__non-collapsable" role="button">
                                <span> <FormattedMessage id="app.info" /></span>
                                </div>
                                <div className="row">
                                    <Col xs={6}>
                                        <h3 className="SingleContentConfigFormBody__contentTitle">
                                        <FormattedMessage id="app.content" />: -
                                        </h3>
                                    </Col>
                                    <Col xs={6} className="SingleContentConfigFormBody__addButtons">
                                        <Link to="/singleconfigpage">
                                            <Button bsStyle="primary">
                                                {this.state.selectedContent.length ? <FormattedMessage id="app.edit" /> : <FormattedMessage id="app.addExistingContent" />}
                                            </Button>
                                        </Link>
                                        <Button className="AddContentTypeFormBody__save--btn" bsStyle="primary" onClick={this.handleAddNewContent}>
                                            {BTN_ADD_NEW_CONTENT}
                                        </Button>
                                    </Col>
                                </div>
                                <div style={{ marginTop: "1rem" }}></div>
                                <SimpleTable
                                    setTemplateId={this.props.setTemplateId}
                                    templateList={this.state.templateList}
                                    content={this.state.selectedContent}
                                    selectedContent={this.state.selectedContent}
                                    selectedTemplateId={this.props.selectedTemplateId}
                                />
                            </div>
                        </div>
                    </Col>
                </Row>
            </form>
        )
    }
}
